import React from "react"
import {DivTitle, H1, Hr,  H2, DivContainer, Projetos, DivContainerProjetos, DivProjeto, Video, ALinkProjetos } from './styled.js'

export default function projeto(){
    return (
        <>
            <DivContainer id="projetos">

                <DivTitle> 
                <H1> PROJETOS </H1>
                <Hr/>
                </DivTitle>

                <DivContainerProjetos>
                    <Projetos>
                        <ALinkProjetos href="https://clscaio.github.io/Pokedex/" target="_blank"> <H2>Pokedex</H2> </ALinkProjetos> 
                        <DivProjeto>
                            <Video controls  src="https://www.youtube.com/embed/ueJXo1b3MyE" alt="Pokedex">
                                Desulpe, seu navegador não suporta vídeos HTML5 em WebM com VP8 ou MP4 com H.264.
                            </Video>
                        </DivProjeto>
                    </Projetos>

                    <Projetos>
                        <ALinkProjetos href="https://clscaio.github.io/API-filme/" target="_blank"> <H2>TMDB-Filmes</H2> </ALinkProjetos> 
                        <DivProjeto>
                            <Video controls  src="https://www.youtube.com/embed/jrlGcHt3O_M" alt="TMDB-Filmes">
                                Desulpe, seu navegador não suporta vídeos HTML5 em WebM com VP8 ou MP4 com H.264.
                            </Video>
                        </DivProjeto>
                    </Projetos>
                </DivContainerProjetos>
            </DivContainer>
        </>
    )
}