import './menu_toggle.css'

export default function menu() {

    function abrirMenu() {
        const menu_operacional = document.getElementById('menu_operacional')
        menu_operacional.classList.toggle('menu_operacional_open')
    }

    return (
        <div className="logo">
            <h1 className="cabecalho_titulo"> Caio Sousa </h1>
            <button 
                className="menu_toggle" 
                onClick={(e) => {
                        abrirMenu()
                }}
            >
                <div className="corpo_toggle">
                    <div className="detalhe_toggle"></div>
                    <div className="detalhe_toggle"></div>
                    <div className="detalhe_toggle"></div>
                </div>
            </button>
        </div>
    )
}