import React from "react"
import './skills.css'

export default function skills() {
    return [
        <div className="container_skills">
            <ul className="container" id="hard_skills">
                <li className="titulo_skills"> <h1> Hard Skills </h1> </li>
                <hr />
                <ul className="container_itens_skills">
                    <li>Java Script</li>
                    <li>HTML</li>
                    <li>CSS</li>
                    <li>React.js</li>
                </ul>
            </ul>

            <ul className="container" id="soft_skills">
                <li className="titulo_skills"><h1> Soft Skills</h1> </li>
                <hr />
                <ul className="container_itens_skills">
                    <li>Pontualidade</li>
                    <li>Organização</li>
                    <li>Comprometimento</li>
                    <li>Facilidade de comunicação</li>
                    <li>Espírito de equipe</li>
                    <li>Adaptabilidade</li>
                    <li id="cursos">Ser autodidata</li>
                </ul>
            </ul>
        </div >
    ]
}