import styled from 'styled-components'
import { DivTitle } from '../projetos/styled'

export const DivContinerCursos = styled.div`
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 30px;
    margin: 30px 0;
`

export const DivContainerConteudoCursos = styled.div`
    display: flex;
    align-items: flex-start;
    justify-content: center;
    max-width: 1280px;
    flex-direction: column;
    gap: 30px;

    .divCurso {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: flex-start;
        width: 330px;
    }

    span {
        color: skyblue;
    }

    p {
        margin: 0 10px;
    }
    
    @media(min-width: 800px ) {
    flex-direction: row;
    } 
`

export const DivTitleCustom = styled(DivTitle)`
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    gap: 10px;
` 