import styled from 'styled-components'

export const DivContainer = styled.div`
   display: flex;
   flex-direction: column;
   padding-top: 50px;
   gap: 40px;
   justify-content: center;
   align-items: center;

   @media (min-width: 1280px){
    width: 1280px;
   }
`

export const DivTitle = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    gap: 10px;

    @media (min-width: 1280px){
    width: 1280px;
   }

   h3{
        color: skyblue;
    }
`

export const Hr = styled.hr`
    width: 100%;
`

export const  DivContainerProjetos = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 40px;

    @media (min-width: 800px) {
        flex-direction: row;
        gap: 100px;
    }
`

export const Projetos = styled.div`
    display: flex;
    gap: 10px;
    flex-direction: column;
`

export const H1 = styled.h1`
    color: #c0c0c0;
`

export const H2 = styled.h1`
    color: skyblue;
    margin-left: 30px;
`

export const DivProjeto = styled.div`
   border: solid 2px skyblue;
   border-radius: 15px;
   overflow: hidden;
`

export const Video = styled.iframe`
    width: 320px;
    height: 180px;
    border: none;

    @media (min-width: 1280px) {
        width: 560px;
        height: 315px;
    }
`

export const ALinkProjetos = styled.a`
    text-decoration: none;

    :hover {
        text-decoration: underline;
        color: skyblue;
        transform: scale(1.15);
        transition: 0.6s;
    }
`